import { status } from '@/alas';
import { defineComponent, reactive } from '@vue/composition-api';
export default defineComponent({
    props: {},
    setup() {
        // =================
        //
        // state
        //
        let $ = reactive({
            project: status.fetch('project')
        });
        // =================
        //
        // methods
        //
        let remove = (id) => {
            $.project.variables.remove(id);
        };
        let addVariable = () => {
            $.project.variables.write({
                key: '',
                value: ''
            });
        };
        // =================
        //
        // done
        //
        return {
            $,
            remove,
            addVariable
        };
    }
});
